/* EventDetails.css */

.event-details-overlay {
  display: flex;
  justify-content: center;
  align-items: flex-end; /* Align to the bottom of the screen */
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  transition: opacity 0.3s ease, transform 0.3s ease; /* Add a smooth transition */
}

.event-details-content {
  background-color: #fefefe;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  width: 80%; /* Adjust the width as needed */
  transform: translateY(100%); /* Initially off-screen */
  transition: transform 0.3s ease; /* Add a smooth transition */
}

.event-details-overlay.open {
  opacity: 1; /* Make the overlay visible */
  transform: translateY(0); /* Slide up to the center of the screen */
}

.close-button {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
  cursor: pointer;
}

.close-button:hover {
  color: black;
}

  